@import "foundation/components/labels";

/* begin labels override */

@mixin cs-label-base {
  display: inline-block;
  font-family: $label-font-family;
  font-weight: $label-font-weight;
  line-height: $label-font-line-height;
  margin-bottom: auto;
  position: relative;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
}


@include exports("label-bem") {
  .badge, .badge-default {
    @include cs-label-base;

    @include label-size;

    @include label-style($gray-base, true);

    &.radius {
      @include label-style(false, true);
    }

    &.round,
    &--round {
      @include label-style(false, $radius: 1000px);
      font-size: $label-round-font-size;
    }

    &.primary, &.badge-primary {
      @include label-style($primary-color);
    }

    &.alert, &.badge-danger {
      @include label-style($alert-color);
    }

    &.warning, &.badge-warning {
      @include label-style($warning-color);
    }

    &.success, &.badge-success {
      @include label-style($success-color);
    }

    &.secondary, &.badge-secondary {
      @include label-style($secondary-color);
    }

    &.info, &.badge-info {
      @include label-style($info-color);
    }

    &:empty {
      display: none;
    }
  }
}