@import "bootstrap/popovers";
/* _popovers.scss override */
/* --------- POPOVER --------- */

.popover{
  @include box-shadow(none);
  padding: 0;
  &.top     { margin-top: -$popover-displacement; }
  &.right   { margin-left: $popover-displacement; }
  &.bottom  { margin-top: $popover-displacement; }
  &.left    { margin-left: -$popover-displacement; }
}

.popover-title{
  border-bottom: 1px solid $gray-dark;
  font-family: $brand-font-extra-light;
  font-size: 16px;
}

.popover-content{
  font-family: $font-family-base;
  font-size:13px;
}
/* --------- END POPOVER --------- */
