.page-sub-header {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid $gray-light-3;
    padding: .75rem 2rem;
    background-color: $white;
    height: 65px;
    &__left {
        flex-grow: 1;
        display: flex;
        align-items: center;
    }
    &__right {
        flex-grow: 1;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .export i {
        color: $gray;
        font-size: 1.25rem;
        padding-left: 1rem;
        margin-left: 1rem;
        cursor: pointer;
    }
    .export-users i {
        color: $gray;
        font-size: 1.5rem;
        padding-right: .5rem;
        margin-right: .5rem;
        cursor: pointer;
        &:hover {
            color: $brand-primary;
            text-decoration: none;
        }
    }
}
