@import "foundation/components/type";

@include exports("type-bem") {
  // Responsive Text alignment
  @include align-classes;

  /* Typography resets */
  div,
  dl,
  dt,
  dd,
  ul,
  ol,
  li,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  pre,
  form,
  p,
  blockquote,
  th,
  td {
    margin: 0;
    padding: 0;
  }

  /* Default Link Styles */
  a {
    color: $anchor-font-color;
    line-height: inherit;
    text-decoration: $anchor-text-decoration;

    &:hover,
    &:focus {
      color: $anchor-font-color-hover;

      @if $anchor-text-decoration-hover != $anchor-text-decoration {
        text-decoration: $anchor-text-decoration-hover;
      }
    }

    &:focus {
      @include focus-styles;
    }

    img {
      border: none;
    }
  }

  /* Default paragraph styles */
  p {
    font-family: $paragraph-font-family;
    font-size: $paragraph-font-size;
    font-weight: $paragraph-font-weight;
    margin-bottom: $paragraph-margin-bottom;
    text-rendering: $paragraph-text-rendering;

    & > code {
      // fix inline code block line-height
      line-height: 1em;
    }

    &.lead {
      //@include lead;
      font-size: $lead-font-size;
    }

    & aside {
      font-size: $paragraph-aside-font-size;
      font-style: $paragraph-aside-font-style;
      line-height: $paragraph-aside-line-height;
    }
  }

  /* Default header styles */
  h1, h2, h3, h4, h5, h6 {
    color: $header-font-color;
    font-family: $header-font-family;
    font-style: $header-font-style;
    font-weight: $header-font-weight;
    line-height: $header-line-height;
    margin-bottom: $header-bottom-margin;
    margin-top: $header-top-margin;
    text-rendering: $header-text-rendering;

    small {
      color: $small-font-color;
      font-size: $small-font-size;
      line-height: 0;
    }
  }

  .title-heading {
    font-size: $title-heading-font-size - $h1-font-reduction;
  }

  h1 {
    font-size: $h1-font-size - $h1-font-reduction;
  }

  h2 {
    font-size: $h2-font-size - $h2-font-reduction;
  }

  h3 {
    font-size: $h3-font-size - $h3-font-reduction;
  }

  h4 {
    font-size: $h4-font-size - $h4-font-reduction;
  }

  h5 {
    font-size: $h5-font-size - $h5-font-reduction;
  }

  h6 {
    font-size: $paragraph-font-size - $h6-font-reduction;
  }

  .subheader {
    @include subheader;
  }

  span {
    font-size: $paragraph-font-size;
  }

  hr {
    border: $hr-border-style $hr-border-color;
    border-width: $hr-border-width 0 0;
    clear: both;
    height: 0;
    margin: $hr-margin 0 ($hr-margin - rem-calc($hr-border-width));
  }

  /* Helpful Typography Defaults */
  em,
  i {
    font-style: italic;
    line-height: inherit;
  }

  strong,
  b {
    font-weight: $font-weight-bold;
    line-height: inherit;
  }

  small {
    font-size: $small-font-size;
    line-height: inherit;
  }

  .body-small {
    font-size: $body-small-font-size;
  }

  .body-smallest {
    font-size: $body-smallest-font-size;
  }

  code {
    background-color: $code-background-color;
    border-color: $code-border-color;
    border-style: $code-border-style;
    border-width: $code-border-size;
    color: $code-color;
    font-family: $code-font-family;
    font-weight: $code-font-weight;
    padding: $code-padding;
  }

  /* Lists */
  ul,
  ol,
  dl {
    font-family: $list-font-family;
    font-size: $list-font-size;
    line-height: $list-line-height;
    list-style-position: $list-style-position;
    margin-bottom: $list-margin-bottom;
  }

  ul {
    margin-#{$default-float}: $list-side-margin;

    &.no-bullet {
      margin-#{$default-float}: $list-side-margin-no-bullet;

      li {
        ul,
        ol {
          margin-#{$default-float}: $list-nested-margin;
          margin-bottom: 0;
          list-style: none;
        }
      }
    }
  }

  /* Unordered Lists */
  ul {
    li {
      ul,
      ol {
        margin-#{$default-float}: $list-nested-margin;
        margin-bottom: 0;
      }
    }

    &.square,
    &.circle,
    &.disc {
      li ul {
        list-style: inherit;
      }
    }

    &.square {
      list-style-type: square;
      margin-#{$default-float}: $list-side-margin;
    }

    &.circle {
      list-style-type: circle;
      margin-#{$default-float}: $list-side-margin;
    }

    &.disc {
      list-style-type: disc;
      margin-#{$default-float}: $list-side-margin;
    }

    &.no-bullet {
      list-style: none;
    }
  }

  /* Ordered Lists */
  ol {
    margin-#{$default-float}: $list-ordered-side-margin;

    li {
      ul,
      ol {
        margin-#{$default-float}: $list-nested-margin;
        margin-bottom: 0;
      }
    }
  }

  /* Definition Lists */
  dl {
    dt {
      margin-bottom: $definition-list-header-margin-bottom;
      font-weight: $definition-list-header-weight;
    }

    dd {
      margin-bottom: $definition-list-margin-bottom;
    }
  }

  /* Abbreviations */
  abbr,
  acronym {
    text-transform: uppercase;
    font-size: 90%;
    color: $body-font-color;
    cursor: $cursor-help-value;
  }

  abbr {
    text-transform: none;

    &[title] {
      border-bottom: $acronym-underline;
    }
  }

  /* Blockquotes */
  blockquote {
    margin: 0 0 $paragraph-margin-bottom;
    padding: $blockquote-padding;
    border-#{$default-float}: $blockquote-border;

    cite {
      display: block;
      font-size: $blockquote-cite-font-size;
      color: $blockquote-cite-font-color;

      &:before {
        content: "\2014 \0020";
      }

      a,
      a:visited {
        color: $blockquote-cite-link-color;
      }
    }
  }

  blockquote,
  blockquote p {
    line-height: $paragraph-line-height;
    color: $blockquote-font-color;
  }

  /* Microformats */
  .vcard {
    display: inline-block;
    margin: $microformat-margin;
    border: $microformat-border-width $microformat-border-style $microformat-border-color;
    padding: $microformat-padding;

    li {
      margin: 0;
      display: block;
    }

    .fn {
      font-weight: $microformat-fullname-font-weight;
      font-size: $microformat-fullname-font-size;
    }
  }

  .vevent {
    .summary {
      font-weight: $microformat-summary-font-weight;
    }

    abbr {
      cursor: $cursor-default-value;
      text-decoration: $microformat-abbr-font-decoration;
      font-weight: $microformat-abbr-font-weight;
      border: none;
      padding: $microformat-abbr-padding;
    }
  }

  @media #{$medium-up} {
    h1, h2, h3, h4, h5, h6 {
      line-height: $header-line-height;
    }

    .title-heading {
      font-size: $title-heading-font-size;
    }

    h1 {
      font-size: $h1-font-size;
    }

    h2 {
      font-size: $h2-font-size;
    }

    h3 {
      font-size: $h3-font-size;
    }

    h4 {
      font-size: $h4-font-size;
    }

    h5 {
      font-size: $h5-font-size;
    }

    h6 {
      font-size: $h6-font-size;
    }
  }
}


/* begin type creator */
@each $type in map-keys($fontSizes) {
  $fontFace: "" !default;
  $lineHeight: "" !default;

  // get font faces
  @if map-has-key($fontFaces, $type) {
    $fontFace: map-get($fontFaces, $type);
  }

  // get font sizes
  $fontSize: map-get($fontSizes, $type);

  // get line heights
  @if map-has-key($lineHeights, $type) {
    $lineHeight: map-get($lineHeights, $type) / (map-get($lineHeights, $type) * 0 + 1);
  }

  // set type baseline padding
  #{$type} {
    @if $fontFace != "" {
      font-family: $fontFace;
    }

    // set baseline spacing
    @include vr-baseline($fontSize, $line-height: $lineHeight);

    // set line height rounded to the nearest pixel
    line-height: $lineHeight + 0.00001;

    // add conversion classes
    @each $tp in map-keys($fontSizes) {
      // set line-height var
      $lh: "" !default;

      // get font size
      $fs: map-get($fontSizes, $tp);

      // get line heights
      @if map-has-key($lineHeights, $tp) {
        $lh: map-get($lineHeights, $tp) / (map-get($lineHeights, $tp) * 0 + 1);
      }

      @if $type != $tp and $fs <= $fontSize {
        // assign utility class and parse underscore
        &.#{$tp} {
          font-size: $fs;
          line-height: $lh;

          // set vertical center utility
          &.v-center {
            line-height: $lineHeight * $fontSize;
          }

          // set text alignment
          &:not(.v-center) {
            @include vr-baseline($fs, $from: $fontSize, $line-height: $lh, $from-line-height: $lineHeight);
          }
        }
      }
    }

    // set vertical center to bottom utility
    &.v-bottom {
      transform: translateY($base-cap-height);
    }
  }

  // extend base classes
  .#{$type} {
    @extend #{$type};
  }
}

/* begin type override */
@include brand-font-family-ciscoFont;

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// debug grid
&.debug-grid {
  &:after {
    content: '';
    height: 100%;
    width: 100%;
    display: inline-block;
    left: 0;
    top: 0;
    z-index: 9998;
    position: absolute;
    pointer-events: none;

    @include show-baseline(rgba(255, 0, 0, 0.1667));
  }

  &:before {
    z-index: 9999;
    pointer-events: none;
  }
}
// set letter spacing for footer/nav
footer, nav {
  h1, h2, h3, h4, h5, h6, p, a, span {
    letter-spacing: normal;
  }
}

.primary-color {
  color: $primary-color;
}

.secondary-color {
  color: $secondary-color;
}

.info-color {
  color: $info-color;
}

.warning-color {
  color: $warning-color;
}

.alert-color {
  color: $alert-color;
}

.text-muted {
  color: $aluminum;
}

/* --------- font --------- */
.lead {
  font-family: $brand-font-regular;
}

