    margin-bottom: 1rem;
  }
  h2, .h2 {
    margin-bottom: 1rem;
  }
  h3, .h3 {
    margin-bottom: 1rem;
  }
  h4, .h4 {
    margin-bottom: 1rem;
  }
  h5, .h5 {
    margin-bottom: 1rem;
  }
