@import "foundation/components/breadcrumbs";

/* begin breadcrumbs override */

.breadcrumbs {
  border: none;
  li {
    font-family: $brand-font-regular;
    @extend .icon-arrow-next;
  }
  & > *:before {
    font-family: $Cisco-Toolkit-Icons;
  }

  & > * span{
    font-size: rem-calc(12);
  }
  .side-panel & a {
    color: $brand-primary;
  }

  & > *.current,
  & > *.current a {
    color: $gray-dark-1;
  }
}
