@import "bootstrap/modals";
/* _modals.scss override */
/* --------- MODAL --------- */

.modal {
  &-header {
    background-color: transparent;
    border: none;
  }
  &-title {
    line-height: 32px;
    font-family: $brand-font-thin;
    font-size: 32px;
    font-weight: normal;
  }
  &-body {
    margin-right: $modal-outer-padding;
    margin-left: $modal-outer-padding;
    background-color: $gray-light-4;
    font-family: $brand-font-light;
    font-size: 14px;
    margin-bottom: 5px;
    p {
      font-family: $brand-font-light;
      font-size: 14px;
    }
    .form-group {
      padding-bottom: 10px;
      &:last-of-type {
        padding-bottom: 0;
      }
    }
    .formly-field {
      padding-bottom: 10px;
      &.last-field {
        padding-bottom: 0;
      }
    }
    &.modal-white {
      background-color: $white;
    }
  }
  &-content {
    top: 65px;
    border-radius: $border-radius-base;
    box-shadow: none;
  }
  &-footer {
    padding: 25px $modal-outer-padding $modal-outer-padding $modal-outer-padding;
    margin: 0;
    border: none;
  }
}
/* --------- END MODAL --------- */
