@import "./bootstrap/progress-bars";
/* _progress-bars.scss override */
/* --------- PROGRESS --------- */

.progress {
  overflow: visible;
  height: 4px;
  @include box-shadow(none);
  @include border-radius(0px);
  background-color: $gray-light;
}

.progress-bar {
  height: 4px;
  margin-top: -1px;
  margin-left: -1px;
  line-height: 12px;
  @include box-shadow(none);
}

.progressbar-info {
  width: 100%;
  line-height: 12px;
  padding-bottom: 5px;
}

.progressbar-label {
  font-family: $font-family-sans-serif;
  font-size: 11px;
  color: $gray-darkest;
  text-align: left;
}

.progressbar-progress {
  font-family: $font-family-sans-serif;
  font-size: 11px;
  color: $gray-darkest;
  float: right;
}

.progress-striped .progress-bar {
  background-size: 20px 20px;
}

.progress-striped .progress-bar-warning {
  @include gradient-striped($brand-warning-dark, 135deg);
}

.progress-striped .progress-bar-danger {
  @include gradient-striped($brand-danger-dark, 135deg);
}

.progress-striped .progress-bar-primary {
  @include gradient-striped($brand-primary-dark, 135deg);
}

.progress-striped .progress-bar-info {
  @include gradient-striped($brand-info-dark, 135deg);
}

.progress-striped .progress-bar-success {
  @include gradient-striped($brand-success-dark, 135deg);
}

// Call animation for the active one
.progress.active .progress-bar {
  @include animation(progress-bar-stripes 2s linear infinite reverse);
}
/* --------- END PROGRESS --------- */
