@import "foundation/components/forms";

// begin forms override

// @MIXIN
//
// We use this mixin to give all basic form elements their style
@mixin form-element {
  transition: border-color ease-out .15s, box-shadow ease-out .15s;
  background-color: $input-bg-color;
  border: {
    style: $input-border-style;
    width: $input-border-width;
    color: $input-border-color;
  }
  color: $input-font-color;
  display: block;
  font-family: $input-font-family;
  font-size: $input-font-size;
  height: ($input-font-size + ($form-spacing * 1.5) - rem-calc(2));
  margin: 0 0 $form-spacing 0;
  padding-top: rem-calc(9);
  padding-right: $form-spacing * .75;
  padding-bottom: rem-calc(7);
  padding-left: $form-spacing * .75;
  width: 100%;

  @include box-sizing(border-box);

  // Basic hover styles
  &:hover {
    border-color: $input-hover-border-color;
  }

  // Basic focus styles
  &:focus {
    background: $input-focus-bg-color;
    border-color: $input-focus-border-color;
    transition: border-color ease-in .15s, box-shadow ease-in .15s;
    outline: none;

    // outline focus fix
    z-index: $input-focus-z-index;
  }

  // Disabled Styles
  &:disabled,
  &.disabled {
    background-color: $input-disabled-bg;
    border-color: $gray-light-2;
    cursor: $input-disabled-cursor;
    color: $gray-light-1;
    &:hover {
      border-color: $input-disabled-bg;
    }
  }

  // Readonly styles
  &[readonly],
  &.readonly {
    background-color: $input-disabled-bg;
    border-color: $input-disabled-bg;
    cursor: $input-disabled-cursor;
    color: $gray-dark-1;
    &:hover {
      border-color: $input-disabled-bg;
    }
  }
}

// We use this mixin to create postfix/prefix form Labels
@mixin prefix-postfix-base {
  border-style: $input-prefix-border-type;
  border-width: $input-prefix-border-size;
  display: block;
  font-size: $form-label-font-size;

  //height: ($input-font-size + ($form-spacing * 1.5) - rem-calc(1));
  //line-height: ($input-font-size + ($form-spacing * 1.5) - rem-calc(1));
  overflow: $input-prefix-overflow;

  //padding-bottom: 0;
  //padding-top: 0;
  position: relative;
  text-align: center;
  width: 100%;

  &:not(.prefix) {
    border-left: none;
  }

  //z-index: 2;
}

// We use this mixin to style select elements
@mixin form-select {
  transition: border-color ease-in .15s, box-shadow ease-in .15s;
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  background-color: $select-bg-color;

  //border-radius: 0;

  // Hide the dropdown arrow shown in newer IE versions
  &::-ms-expand {
    display: none;
  }

  // This custom double arrow has some fake horizontal padding so we can align it
  // from the right side of the element without relying on CSS3
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' height='100%' viewBox='-35 -25 100 100'><polyline points='35,20 25,5 15,20'/><polyline points='35,30 25,45 15,30'/></svg>");

  // We can safely use leftmost and rightmost now
  background-position: if($text-direction == "rtl", 0%, 100%) center;
  background-repeat: no-repeat;
  border: {
    style: $input-border-style;
    width: $input-border-width;
    color: $input-border-color;
  };
  color: $input-font-color;
  font-family: $input-font-family;
  font-size: $input-font-size;
  line-height: normal;
  padding: $form-spacing / 2 - rem-calc($input-border-width);

  &:focus {
    transition: border-color ease-in .15s, box-shadow ease-in .15s;
    background-color: $select-hover-bg-color;
    border-color: $input-focus-border-color;

    @include box-shadow(0 0 2px 1px $focus-color);
  }

  // Disabled Styles
  &:disabled {
    background-color: $input-disabled-bg;
    cursor: $input-disabled-cursor;
  }
}

@include exports("form-bootstrap") {

  .secondary-label {
    color: $form-label-font-color;
    margin-left: floor(($grid-gutter-width / 2));
  }

  // Attach elements to the beginning or end of an input
  .prefix,
  .postfix {
    @include prefix-postfix-base;

    @include vr-baseline($input-font-size, $from: 20.5, $border: $input-border-width, $baseline: false);
  }

  // Adjust padding, alignment and radius if pre/post element is a button
  .postfix.button {
    @include button-size(false, false);

    @include postfix(false, false, true);
  }

  .prefix.button {
    @include button-size(false, false);

    @include prefix(false, false, true);
  }

  .prefix.round {
    @include radius(0);

    @include side-radius($default-float, $button-round);
  }

  .postfix.round {
    @include radius(0);

    @include side-radius($opposite-direction, $button-round);
  }

  // Separate prefix and postfix styles when on span or label so buttons keep their own
  span.prefix, label.prefix {
    @include prefix;
  }

  span.postfix, label.postfix {
    @include postfix;
  }

  .form-control {
    width: 100%;
  }

  form {


    .row {
      .prefix-radius.row.collapse {
        input,
        textarea,
        select,
        button {
          @include radius(0);

          @include side-radius($opposite-direction, $button-radius);
        }

        .prefix {
          @include radius(0);

          @include side-radius($default-float, $button-radius);
        }
      }

      .postfix-radius.row.collapse {
        input,
        textarea,
        select,
        button {
          @include radius(0);

          @include side-radius($default-float, $button-radius);
        }

        .postfix {
          @include radius(0);

          @include side-radius($opposite-direction, $button-radius);
        }
      }

      .prefix-round.row.collapse {
        input,
        textarea,
        select,
        button {
          @include radius(0);

          @include side-radius($opposite-direction, $button-round);
        }

        .prefix {
          @include radius(0);

          @include side-radius($default-float, $button-round);
        }
      }

      .postfix-round.row.collapse {
        input,
        textarea,
        select,
        button {
          @include radius(0);

          @include side-radius($default-float, $button-round);
        }

        .postfix {
          @include radius(0);

          @include side-radius($opposite-direction, $button-round);
        }
      }
    }

    // bootstrap alias
    &.form {
      // form horizontal
      &-horizontal {
        label, .form-control-static {
          @include vr-baseline($input-font-size, $from: 20.5, $border: -0.377px, $baseline: false);
          word-wrap: break-word;
          margin-bottom: $form-spacing / 2 ;

          &.control-label {
            text-align: right;
          }
        }
      }

      // form-inline
      &-inline {
        input,
        textarea,
        select,
        button {
          @extend .column;

          width: auto !important;

          @if $text-direction == "rtl" {
            margin-right: $form-spacing;
          }
          @else {
            float: left !important;
            margin-left: $form-spacing;
          }
        }

        label {
          display: none;
        }
      }
    }
  }

  // .form-group extend for bootstrap alias
  .row {
    & .form-group {
      display: block;

      @extend .clearfix;

      @extend %collapse-full;

      @extend %collapse-left;

      @extend %collapse-right;
    }
  }
}

.form-group {
  @include clearfix;
}
.form-group, .formly-field-group {
  margin-bottom: $form-spacing / 2;
}

.form-control-feedback {
  position: absolute;
  top: 6px;
  right: 0;
  .has-error & i {
    color: $brand-danger;
    cursor: pointer;
  }
}

.has-error .form-control, .has-error .form-inline {
    border-color: transparent;
    box-shadow: 0 0 0 2px $brand-danger;
}

.formly-input-wrapper[class*=col-] {
  float: left;
}

.description-block {
  display: block;
  margin-top: 5px;
  margin-bottom: 10px;
  color: $steel;
}

// Inline Forms
@mixin form-inline {

  // Kick in the inline
  @media (min-width: $screen-sm-min) {
    // Inline-block all the things for "inline"
    .form-group {
      width: auto;
      display: inline-block;
      margin-bottom: 0;
      vertical-align: middle;
    }

    .formly-field-inline &.formly-field-input input {
      margin-bottom: 0;
    }

    // In top-bar-form, allow folks to *not* use `.form-group`
    .form-control {
      display: inline-block;
      width: auto; // Prevent labels from stacking above inputs in `.form-group`
      vertical-align: middle;
      word-wrap: break-word;
    }

    // Make static controls behave like regular ones
    .form-control-static {
      display: inline-block;
      word-wrap: break-word;
    }

    .input-group {
      display: inline-table;
      vertical-align: middle;

      .input-group-addon,
      .input-group-btn,
      .form-control {
        width: auto;
      }
    }

    // Input groups need that 100% width though
    .input-group > .form-control {
      width: 100%;
    }

    .control-label {
      margin-bottom: 0;
      vertical-align: middle;
    }

    // Remove default margin on radios/checkboxes that were used for stacking, and
    // then undo the floating of radios and checkboxes to match.
    .radio,
    .checkbox {
      display: inline-block;
      margin-top: 0;
      margin-bottom: 0;
      vertical-align: middle;

      label {
        padding-left: 0;
      }
    }
    .radio input[type="radio"],
    .checkbox input[type="checkbox"] {
      position: relative;
      margin-left: 0;
    }

    // Re-override the feedback icon.
    .has-feedback .form-control-feedback {
      top: 0;
    }
  }
}
form.form-inline, .form-inline {
  @include form-inline;
  label {
    display: block
  }
  input {
    margin-left: 0
  }
  .input-group-btn {
    display: table-cell
  }
  .input-group {
    .btn {
      margin-left: 0
    }
  }
}

// Horizontal forms
//
// Horizontal forms are built on grid classes and allow you to create forms with
// labels on the left and inputs on the right.

form.form-horizontal {

  // Consistent vertical alignment of radios and checkboxes
  //
  // Labels also get some reset styles, but that is scoped to a media query below.
  .radio,
  .checkbox,
  .radio-inline,
  .checkbox-inline {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: ($padding-base-vertical + 1); // Default padding plus a border
  }
  // Account for padding we're adding to ensure the alignment and of help text
  // and other content below items
  .radio,
  .checkbox {
    min-height: ($line-height-computed + ($padding-base-vertical + 1));
  }

  // Make form groups behave like rows
  .form-group {
    margin-left: 0;
    margin-right: 0;
  }

  // Reset spacing and right align labels, but scope to media queries so that
  // labels on narrow viewports stack the same as a default form example.
  @media (min-width: $screen-sm-min) {
    label.control-label{
      text-align: right;
      margin-bottom: 0;
      padding-top: ($padding-base-vertical + 1); // Default padding plus a border
    }
  }

  // Validation states
  //
  // Reposition the icon because it's now within a grid column and columns have
  // `position: relative;` on them. Also accounts for the grid gutter padding.
  .has-feedback .form-control-feedback {
    right: -1 * floor(($grid-gutter-width / 2));
  }

  // Form group sizes
  //
  // Quick utility class for applying `.input-lg` and `.input-sm` styles to the
  // inputs and labels within a `.form-group`.
  .form-group-lg {
    @media (min-width: $screen-sm-min) {
      .control-label {
        padding-top: (($padding-large-vertical * $line-height-large) + 1);
        font-size: $font-size-large;
      }
    }
  }
  .form-group-sm {
    @media (min-width: $screen-sm-min) {
      .control-label {
        padding-top: ($padding-small-vertical + 1);
        font-size: $font-size-small;
      }
    }
  }
}
