 .messagebox-backdrop,
 .messagebox {
   position: fixed;
   height: 100vh;
   background: center center / cover no-repeat fixed;
   width: 100vw;
   color: $gray-dark-3;
   display: flex;
   align-items: center;
   justify-content: center;
   .messagebox-small,
   .message-box {
     width: vr-adjust(350);
     min-height: vr-adjust(410);
     background: $color-white;
     text-align: center;
     @include vr-spacing(pt, 3);
     @include vr-spacing(ph, 1.5);
     @include vr-spacing(pb, 1.5);
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     section {
       @include vr-spacing(mb, 2);
     }
     &__icon {
       @extend .icon;
       color: $gray-dark-1;
       font-size: 5rem;
       @include vr-spacing(mb, 2);
       display: block;
     }
     &__title {
       @extend .h3;
       @include vr-spacing(mb, 1.5);
       word-wrap: break-word;
       &--attention {
         color: $color-attention;
       }
       &--alert {
         color: $color-alerts;
       }
       &--success {
         color: $color-cta;
       }
     }
     &__details,
     p {
       @extend .h5;
       font-size: rem(20);
       text-align: center;
       @include vr-spacing(mb, 1.5);
       word-wrap: break-word;
       // @include vr-spacing(pb, 3);
       // @include vr-spacing(ph, 3);
       color: $gray-dark-1;
     }
     .icon-cisco-logo {
       color: $color-primary-base;
     }
     .messagebox-btn,
     &__btn {
       position: relative;
       bottom: 0;
     }
   }
 }
