@import "foundation/components/reveal";

/* begin reveal override */

@include exports("reveal-ui") {

    // Reveal Modals
    .reveal-modal-bg { @include reveal-bg; }
    
    .modal-open {
      overflow: hidden;
    }

    .#{$reveal-modal-class} {
      @include reveal-modal-base;
      @include reveal-modal-style(
        $bg:$reveal-modal-bg,
        $padding:$reveal-modal-padding,
        $border:true,
        $box-shadow:true,
        $radius:false,
        $top-offset:$reveal-position-top
      );
      position: fixed;

      &.radius { @include reveal-modal-style($radius:true); }
      &.round  { @include reveal-modal-style($radius:$reveal-round); }
      &.collapse { @include reveal-modal-style($padding:0); }
      &.dialog {
        @include reveal-modal-base(false, 380px);
        padding: 0;
        outline: none;
        .modal-content {
          position: static;
          border: none;
        }
        .close {
          display: none;
        }
        .modal-header {
          padding: 24px;
          padding-bottom: 1rem;
        }
        .modal-title {
          text-align: center;
          font-size: 20px;
          color: $gray-dark-2;
          font-family: $brand-font-light;
        }
        .modal-body {
          background: $brand-white;
          text-align: center;
          margin-bottom: 2rem;
        }
        .modal-footer {
          display: flex;
          padding: 0;
          .btn {
            flex: 1;
            background: $white;
            border-top: 1px solid $gray-light-3;
            border-radius: 0;
            color: $gray-dark-1;
            line-height: 1.6;
            &:hover {
              background: $gray-light-4;
            }
            &:disabled, &.disabled {
              color: $button-font-color-disabled;
              &:hover,
              &:focus {
                background-color: $white;
                color: $button-font-color-disabled;
              }
            }
            &--primary, &-primary {
              color: $color-primary-base;
              &:hover, &:active {
                color: $white;
                background: $color-primary-base;
              }
              &:active {
                background: $color-primary-dark;
              }
            }
            &--alert, &--negative {
              color: $color-negative-base;
              &:hover, &:active {
                color: $white;
                background: $color-negative-base;
              }
              &:active {
                background: $color-negative-dark;
              }
            }
            + .btn {
              margin-left: 0;
              border-left: 1px solid $gray-light-3;
            }
          }
        }
      }
      &.full {
        @include reveal-modal-base(false, 100%);
        height: 100vh;
        left:0;
        margin-left: 0 !important;
        max-width: none !important;
        min-height: auto;
        top: 24px;
        padding: 0;
        overflow: auto;
        background: transparent;
        box-shadow: none;
        outline: none;
        
        .modal-title {
          max-width: 900px;
          margin: 0 auto;
          font-size: 36px;
          line-height: 48px;
          color: $brand-white;
          font-family:  $brand-font-extra-light;
        }
        .modal-header {
          background: $color-primary-base;
          padding: 16px;
          width: 100%;
          flex: 0 0 76px;
          .close {
            color: $white;
            text-shadow: none;
            &:before {
              font-size: 20px;
            }
          }
        }
        .modal-content, .modal-content > [ui-view], .modal-content > [ui-view] > [ui-view] {
          display: flex;
          flex-direction: column;
          height: calc(100vh - 48px);
          max-width: 60.25rem;
          margin: 0 auto;
          position: static;
          background: $gray-light-4;
          border: none;
          border-radius: 0;
          &.small {
            max-width: 40rem;
            max-height: 500px;
            .modal-header {
              flex: 0 0 50px;
              padding: 12px;
            }
            .modal-title {
              font-size: 20px;
              line-height: 32px;
              font-family: $brand-font-light;
            }
            .modal-footer {
              flex: 0 0 64px;
              padding: 16px;
              padding-top: 12px;
            }
            .modal-body {
              > * {
                max-width: 590px;
              }
            }
          }
        }
        .modal-content > [ui-view], .modal-content > [ui-view] > [ui-view] {
          margin: 0;
        }
        .modal-body{
          display: flex;
          flex-direction: column;
          flex: 1 1 auto;
          align-self: stretch;
          align-items: center;
          overflow-x: hidden;
          overflow-y: auto;
          background: $brand-white;
          margin: 0;
          padding: rem-calc(24) 0;
          > * {
            max-width: 900px;
            width : 100%;
            flex-shrink: 0;
          }
        }
        .modal-footer {
          text-align: right;
          background: $gray-light-4;
          width: 100%;
          padding: 20px 2rem;
          flex: 0 0 76px;
          width : 100%;
        }

        @media (max-width: $screen-md-min) {
          .modal-title {
            padding-left: calc(10vw - 12px);
          }
          .modal-body{
            padding-left: 10vw;
            padding-right: 10vw;
          }
        }
      }

      // Modals pushed to back
      &.toback {
        z-index: $z-index-base - 2;
      }

      .#{$close-reveal-modal-class} { @include reveal-close; }
    }
}

.reveal-modal-bg.fade.in {
  z-index: 10000;
}

//Till Bootstrap is removed

.modal-alt .modal-content {
  top: 0;
  background: transparent;
}

.#{$reveal-modal-class} {
  .modal-body {
    padding: 0;
  }
}

.reveal-bottom {
  transform: translateY(100%);
  transition: transform 0.3s;
  &.in {
    transform: translateY(0);
  }
}
