@import "foundation/components/dropdown";

/* begin dropdown override */

@include exports("dropdown-ui") {
  .dropdown, [cs-dropdown] {
    position: relative;
    display: inline-block;
  }

  /* Foundation Dropdowns */
  .actions-menu.open .dropdown-menu {
    left: auto;
    right: 14px;
  }

  .dropdown-menu {
    @include dropdown-container(list, false);

    @include radius($f-dropdown-radius);
    border-color: $gray-light-1;
    top: calc(100% + 2px);
    width: auto;
    max-height: rem-calc(261);

    &:not(.nested) {
      overflow-y: auto;
    }

    z-index: $f-dropdown-z-index;

    .sub-menu {
      @extend %dropdown-menu;

      display: none;
    }

    .open & {
      &:not(.sub-menu) {
        display: block !important;
      }

      left: 0;

      .sub-menu {
        left: calc(100% - 1px);
        padding-left: inherit;
      }
    }

    // vertical dropdown styles for isVisible method inside dropdown/dropdown.js
    [cs-dropdown] & {
     &:not(.sub-menu) {
       visibility: hidden;
     }

     &.visible {
       visibility: visible;
     }
    }

    li {
      @include dropdown-style;

      a {
        white-space: nowrap;
      }
    }

    .divider {
      height: 1px;
      margin: 9px 0;
      overflow: hidden;
      background-color: #ccc;
    }

    .pull-right > & {
      right: 0;
      left: auto;
    }

    // You can also put custom content in these dropdowns
    &.content {
      @include dropdown-container(content, $triangle: false);
    }

    &.top {
      top: auto;
      bottom: 100%;
      margin-bottom: 2px;
    }

    // Sizes
    &.tiny {
      max-width: 200px;
    }

    &.small {
      max-width: 300px;
    }

    &.medium {
      max-width: 500px;
    }

    &.large {
      max-width: 800px;
    }

    &.mega {
      width: 100% !important;
      max-width: 100% !important;

      &.open {
        left: 0 !important;
      }
    }
  }

  %dropdown-menu {
    @extend .dropdown-menu;
  }

  .dropdown.open {
    i {
      color: $color-primary-darker;
    }
  }

  .dropdown-primary {
    background: $brand-white;
    border: none;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, .18);

    li {
      &:hover {
        background: $gray-light-3;
      }

      a {
        color: $gray-dark-2;
        padding: 6px 16px;
      }
    }
  }
}
